import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { ApiService } from '../../services/api.service';
import { LoadingController, ToastController } from '@ionic/angular';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  submitted = false;
  role;
  admin: any = {
    username: null,
    password: null,
  };

  tenant: any = {
    room: null,
    phone: null,
  };
  loader;

  constructor(
    public location: Location,
    private router: Router,
    private api: ApiService,
    public toastController: ToastController,
    private loadingController: LoadingController
  ) {}

  ngOnInit() {
    this.role = JSON.parse(localStorage.getItem('role'));
  }

  async presentToast(msg, toastClass) {
    await this.toastController
      .create({
        message: msg,
        cssClass: toastClass,
        duration: 2000,
      })
      .then((res) => res.present());
  }

  async presentLoading() {
    await this.loadingController
      .create({
        cssClass: 'my-custom-class',
        message: 'Please wait...',
      })
      .then((res) => {
        this.loader = res;
        console.log(this.loader.dismiss);
        res.present();
      });
  }

  login(role, form) {
    this.submitted = true;
    if (form.invalid) {
      this.presentToast('Please fill in all credentials!', 'failed');
      return false;
    } else {
      if (role !== 't') {
        this.presentLoading();
        this.api.loginAdmin('admin/login', form.value).subscribe((res) => {
          if (!res.err) {
            this.loader?.dismiss();
            localStorage.setItem('role', JSON.stringify(res.role.id));
            localStorage.setItem('user', JSON.stringify(res));
            this.presentToast('Logged in', 'success');
            this.router.navigate(['landing']);
          } else {
            this.loader?.dismiss();
            this.presentToast(res.err, 'failed');
          }
        });
      } else if (role === 't') {
        this.presentLoading();
        this.api.loginTenant('tenant/login', form.value).subscribe((res) => {
          if (!res.err) {
            console.log(res);
            this.loader?.dismiss();
            localStorage.setItem('role', JSON.stringify(res.role.id));
            localStorage.setItem('user', JSON.stringify(res));
            this.presentToast('Logged in', 'success');
            this.router.navigate(['landing']);
          } else {
            this.loader?.dismiss();
            this.presentToast(res.err, 'failed');
          }
        });
      }
    }
  }

  goBack() {
    this.location.back();
    localStorage.clear();
  }
}
